import React from "react";
import { Link } from "react-router-dom";

const Aside = () => {
  return (
    <>
      <aside>
        <ul>
          <li>
            <Link to="/project/board"></Link>
          </li>
          <li>
            <Link to="/calendar"></Link>
          </li>
          <li>
            <Link to="/newPage"></Link>
          </li>
          <li>
            <Link to="/board/list"></Link>
          </li>
          <li>
            <Link to="/chat"></Link>
          </li>
          <li>
            <Link to="#"></Link>
          </li>
        </ul>
      </aside>
    </>
  );
};

export default Aside;
