import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const Register = () => {
    const navigate = useNavigate();

    const [user, setUser] = useState({
        uid: '',
        pass: '',
        pass2: '',
        name: '',
        nick: '',
        email: '',
        hp: '',
        grade: 'BASIC',
        role: 'USER',
    });

    const submitHandler = (e) => {
        e.preventDefault();

        console.log(user);

        if (user.pass !== user.pass2) {
            alert('    .');
            return;
        }

        axios
            .post('http://localhost:8080/user', user)
            .then((response) => {
                console.log(response.data);
                alert(' !');

                navigate('/user/login');
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const changeHandler = (e) => {
        e.preventDefault();
        setUser({ ...user, [e.target.name]: e.target.value });
    };

    return (
        <>
            <div className="container">
                <div className="signup-container">
                    <div className="signup-Form">
                        <form onSubmit={submitHandler}>
                            <label htmlFor="uid"></label>
                            <input
                                type="text"
                                name="uid"
                                placeholder="Enter your ID"
                                onChange={changeHandler}
                                value={user.uid}
                            />

                            <label htmlFor="password"></label>
                            <input
                                type="password"
                                name="pass"
                                placeholder="Enter your PW"
                                value={user.pass}
                                onChange={changeHandler}
                            />

                            <label htmlFor="confirm-password"> </label>
                            <input type="password" name="pass2" placeholder="Check your PW" onChange={changeHandler} />

                            <label htmlFor="name"></label>
                            <input
                                type="text"
                                name="name"
                                placeholder="Enter your name"
                                value={user.name}
                                onChange={changeHandler}
                            />

                            <label htmlFor="nick"></label>
                            <input
                                type="text"
                                name="nick"
                                placeholder="Enter your nick"
                                value={user.nick}
                                onChange={changeHandler}
                            />

                            <label htmlFor="email"></label>
                            <input
                                type="email"
                                name="email"
                                placeholder="Enter your email"
                                value={user.email}
                                onChange={changeHandler}
                            />
                            <button className="email-verify" type="button">
                                 
                            </button>

                            <label htmlFor="hp"></label>
                            <input
                                type="text"
                                name="hp"
                                placeholder="Enter your HP"
                                value={user.hp}
                                onChange={changeHandler}
                            />

                            <button type="submit"></button>
                        </form>
                        <div className="signup-options">
                            <div className="signup-buttons">
                                <button className="kakao-signup"></button>
                                <button className="google-signup"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Register;
